import React from 'react';

import Box from '@src/components/box/Box';

import '@src/components/layout/components/top.scss';

class Top extends React.PureComponent {

	render() {

		if (window.device) {

			var DeviceInfo = <Box box={{title:"New Luxul Product", theme:"global"}}>
				<div className="form">
					<p>Model #: {window.device.info.model}</p>
					<p>Firmware #: {window.device.info.firmware}</p>
				</div>
			</Box>;

			var Details = <span className="details">
				<span className="link" onClick={()=>{ window.state.side = {Box:DeviceInfo}; }}>
					<span className="title" style={{textIndent:"-1.5px"}}><span className="fontIcon icon-carat_down" /> {window.device.info.model}</span>
					<span className="text" style={{textIndent:"-1px"}}>Firmware: {window.device.info.firmware}</span>
				</span>
			</span>;

		}

		return (
			<div className="top">
				
				<span className="logo" onClick={()=>{ this.props.router.push('/login'); }}>
					<img src="/branding/logo/logo_white.svg" alt="LUXUL" />
				</span>
				
				{Details || null}
				
			</div>
		)
	}
}

export default Top;


