import React from 'react';

import '@src/components/layout/components/side.scss';



// only local data
// {this.state} and {this.props}
class Side extends React.PureComponent {

	render() {

		const { side={} } = this.props;
		
		return (
			<div className={ "sideBox" + ( side.Box ? ' opened' : '' ) }>
				<div className="sideBox_overlay" onClick={()=>{ window.state.side = {}; }} />
				<div className="sideBox_content">
					<div className="sideBox_close" onClick={()=>{ window.state.side = {}; }}>
						<span className="fontIcon icon-cross" title="Close" />
					</div>

					{side.Box||null}

				</div>
			</div>
		)
	}
}



// connect global data
// when {window.state.side} changes, update {this.state.side}
class SideConnected extends React.PureComponent {
	constructor(){
		super();
		this.state = {};
	};
	componentWillMount(){
		this._isMounted = true;

		window.state.watch("side",(name,oldValue,value)=>{
			if (this._isMounted) {
				this.setState({[name]:value});
			}
			return value || {};
		});
	}
	componentWillUnmount(){
		this._isMounted = false;

		window.state.unwatch("side");
	}
	render(){
		return (
			<Side side={this.state.side} />
		)
	}
}



export default SideConnected;


