/* core */
import React from 'react';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import { LuxulForm, LuxulInput, validations } from '@src/components/form/LuxulForm';
import "@src/pages/session/PageLogin.scss";


/*
	form
*/
class PageComponent extends React.Component {

	constructor() {
		super();
		this.state = {
			luxulFormButtons: false,
			luxulFormValues: {
				username:"",
				password:""
			}
		}
	}

	render() {

		var { router } = this.props;

		var formSubmit = (valid) => {

			this.setState({"luxulFormSubmitting": true});

			// dispatch(layoutActions.message({}));
			valid
			.then((event)=>{
				// form is valid
				this.props.route.ubus.login(this.state.luxulFormValues)
				.then((action)=>{
					this.setState({"luxulFormSubmitting": false});
					router.push('/');
					setTimeout(function(){
						window.state.message = {title:"Welcome back!",type:"success"};
					},0);
				})
				.catch((err)=>{ 
					// dispatch(layoutActions.message({title:"Login failed. Please check that your device is connected and powered on.", status:1}));
					this.setState({"luxulFormSubmitting": false});
					window.state.message = {title:"Login failed. Please check that your device is connected and powered on.",type:"error"};

				});

			})
			.catch((err)=>{
				// form is NOT valid
				console.error("Error: "+err);
			});
		};


		var HelpBox = <Box box={{title:"Login Help"}}>
			<div className="form" style={{minWidth:"200px"}}>
				<p>Username: root<br />Password: admin</p>
			</div>
		</Box>;
		const TopChildren = [
			<span key="Login Help" className="fontIcon icon-pk_question" onClick={()=>{ window.state.side = {Box:HelpBox} }}></span>
		];


		return (
			<Layout router={this.props.router} route={this.props.route} pages={false}>
				
				<Box box={{title:this.props.route.page.title, id:"boxLogin", TopChildren:TopChildren}}>
					
					<LuxulForm 
					stateScope={this} 
					onSubmit={formSubmit}
					>
						
						<div className="formSection">
							<fieldset>
								<label>Username:</label>
								<LuxulInput stateScope={this} name="username" type="text" validations={[validations.required]} />
							</fieldset>
							<fieldset>
								<label>Password:</label>
								<LuxulInput stateScope={this} name="password" type="password" validations={[validations.required]} />
							</fieldset>
						</div>

						<div className="formSection">
							<button className={((this.state.luxulFormSubmitting || (this.state.luxulFormInvalid && this.state.luxulFormInvalid.length)) ? " " : " primary")} type="submit"><b>Enter</b></button>
						</div>

					</LuxulForm>

				</Box>

			</Layout>
		)
	}

};


export default PageComponent;


