import React from 'react';

import '@src/components/layout/components/popup.scss';



// only local data
// {this.state} and {this.props}
class Popup extends React.PureComponent {

	render() {

		const { popup={} } = this.props;
		
		return (
			<div className={ "popupBox" + ( popup.Box ? ' opened' : '' ) }>
				<div className="popupBox_overlay" onClick={()=>{ window.state.popup = {}; }} />
				<div className="popupBox_content">
					<div className="popupBox_close" onClick={()=>{ window.state.popup = {}; }}>
						<span className="fontIcon icon-cross" title="Close" />
					</div>

					{popup.Box||null}

				</div>
			</div>
		)
	}
}




// connect global data
// when {window.state.popup} changes, update {this.state.popup}
class PopupConnected extends React.PureComponent {
	constructor(){
		super();
		this.state = {};
	};
	componentWillMount(){
		this._isMounted = true;

		window.state.watch("popup",(name,oldValue,value)=>{
			if (this._isMounted) {
				this.setState({[name]:value});
			}
			return value || {};
		});
	}
	componentWillUnmount(){
		this._isMounted = false;

		window.state.unwatch("popup");
	}
	render(){
		return (
			<Popup popup={this.state.popup} />
		)
	}
}



export default PopupConnected;


