/*eslint no-unused-vars: 2*/
import $ from "jquery";
import React from 'react';
import styled from 'styled-components';

function isNotEqualShallow(a, b) {
	for(let key in a) {
		if(!(key in b) || a[key] !== b[key]) {
			return false;
		}
	}
	for(let key in b) {
		if(!(key in a) || a[key] !== b[key]) {
			return false;
		}
	}
	return true;
}
function isEqualShallow(a, b) {
	if (a===b) {
		return true;
	}
	return !isNotEqualShallow(a, b);
}
function handleHint(title, description){
	if (title && description) {
		this.setState({"Hints": 
		<div>
	            <div className="LuxulHintBox_overlay" onClick={()=>{ this.setState({Hints:""}); }} />
			<div className="LuxulHintBox_content">
				<span className="hintX fontIcon icon-cross" title="Close" onClick={()=>{ this.setState({Hints:""}); }} />
				<h4 className="hintTitle">{title}</h4>
				{description}
			</div>
		</div>
		});
	} else {
		this.setState({"Hints":""});
	}
	this.forceUpdate();
}
function _get_property(itemsObject, name, value){
	const nameArray = name.split('.');
	var formValue;
	if (nameArray[1]) {
		if (itemsObject[nameArray[0]]) {
			formValue = itemsObject[nameArray[0]][nameArray[1]] || "";
		} else {
			formValue = "";
		}
	} else {
		formValue = itemsObject[name] || "";
	}
	return formValue;
}
function _set_property(itemsObject, name, value){
	const nameArray = name.split('.');
	if (nameArray[1]) {
		itemsObject[nameArray[0]][nameArray[1]] = value;
	} else {
		itemsObject[name] = value;
	}
}


/* exports */
const moduleExports = {

	/*
		validations
	*/
	validations: {
		required: function(value) {
			return value ? undefined : (this && this.message ? this.message : 'Required');
		}, 
		fullname: function(value) {
			var re = /^[a-z]([-']?[a-z]+)*( [a-z]([-']?[a-z])*){1,2}$/i;
			return !value || re.test(value) ? undefined : (this && this.message ? this.message : 'Must be full name. Middle name or initial optional');
		}, 
		email: function(value) {
			var re = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
			return !value || re.test(value) ? undefined : (this && this.message ? this.message : 'Invalid email');
		}, 
		zipcode: function(value) {
			var re = /(^\d{5}$)|(^\d{5}-\d{4}$)/;
			return !value || re.test(value) ? undefined : (this && this.message ? this.message : 'Invalid zipcode');
		}, 
		ipv4: function(value) {
			var re = /^(([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])$/g;
			return !value || re.test(value) ? undefined : (this && this.message ? this.message : 'Invalid IP address');
		}, 
		ipv6: function(value) {
			var re = /^s*((([0-9A-Fa-f]{1,4}:){7}([0-9A-Fa-f]{1,4}|:))|(([0-9A-Fa-f]{1,4}:){6}(:[0-9A-Fa-f]{1,4}|((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3})|:))|(([0-9A-Fa-f]{1,4}:){5}(((:[0-9A-Fa-f]{1,4}){1,2})|:((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3})|:))|(([0-9A-Fa-f]{1,4}:){4}(((:[0-9A-Fa-f]{1,4}){1,3})|((:[0-9A-Fa-f]{1,4})?:((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){3}(((:[0-9A-Fa-f]{1,4}){1,4})|((:[0-9A-Fa-f]{1,4}){0,2}:((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){2}(((:[0-9A-Fa-f]{1,4}){1,5})|((:[0-9A-Fa-f]{1,4}){0,3}:((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){1}(((:[0-9A-Fa-f]{1,4}){1,6})|((:[0-9A-Fa-f]{1,4}){0,4}:((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3}))|:))|(:(((:[0-9A-Fa-f]{1,4}){1,7})|((:[0-9A-Fa-f]{1,4}){0,5}:((25[0-5]|2[0-4]d|1dd|[1-9]?d)(.(25[0-5]|2[0-4]d|1dd|[1-9]?d)){3}))|:)))(%.+)?s*(\/([0-9]|[1-9][0-9]|1[0-1][0-9]|12[0-8]))?$/igm;
			return !value || re.test(value) ? undefined : (this && this.message ? this.message : 'Invalid IP address version 6. Example: 0:0:0:0:0:0:0:0 or ::');
		}
	},

	/*
		button
	*/
	LuxulButton: class LuxulButton extends React.Component {

	},

	/*
		form
	*/
	LuxulForm: class LuxulForm extends React.Component {

		constructor(props) {
			super(props);

			/*
				to use this LuxulForm and LuxulInput, must pass stateScope={this} in parent/page
			*/
			var { stateScope } = this.props;
			
			/*
				stateScope.state
				from parent/page would be [this.state]
			*/
			stateScope.state.luxulFormInvalid = stateScope.state.luxulFormInvalid || [];
			stateScope.state.luxulFormValues = stateScope.state.luxulFormValues || {};
			stateScope.state.luxulFormValuesOriginal = JSON.parse(JSON.stringify(stateScope.state.luxulFormValues));
			stateScope.state.luxulFormButtons = stateScope.state.luxulFormButtons!==undefined ? stateScope.state.luxulFormButtons : {};
			
			/*
				stateScope 
				from parent/page would be just [this]
			*/
			// elements
			stateScope.luxulFormElementsToValidate = stateScope.luxulFormElementsToValidate || {};
			
			// validate
			stateScope.luxulFormValidate = function(clearValidations){
				var form = this.form;
				var promise = new Promise((resolve, reject)=>{

					// run through each validated field and check it
					for (let name in stateScope.luxulFormElementsToValidate) {
						if (stateScope.luxulFormElementsToValidate[name]) {
							let comp = stateScope.luxulFormElementsToValidate[name];
							let event = { target: form[name] };
							if (event.target) {
								var invalid = comp.handleValidate({event, value:stateScope.state.luxulFormValues[name], clearValidations});
								if (invalid) {
									reject(invalid);
								}
							}
						}
					}

					// all ok
					resolve(event, stateScope.state.luxulFormValues);

				});
				return promise;
			}.bind(this);

			// reset
			stateScope.luxulFormReset = function(){
				var { stateScope } = this.props;
				// scroll to top
				if (window.innerWidth<=750) {
					// mobile - scroll body
					$("body").animate({
						scrollTop: 0
					}, 1000);
				} else {
					// desktop - scroll box
					$(this.form).animate({
						scrollTop: 0
					}, 1000);
				}
				// run through each validated field and check it (no, reset it!)
				stateScope.luxulFormValidate(true);
				// ok
				stateScope.setState({luxulFormValues:JSON.parse(JSON.stringify(stateScope.state.luxulFormValuesOriginal)), luxulFormTouched:false, luxulFormChanged:false, luxulFormInvalid:[]});
			}.bind(this);
		}

		/*
			scroll
		*/
		componentDidMount() {
			var form = this.form;
			if (form) {
				setTimeout(()=>{
					this.scrollVerticallyEnter(form);
				},500);
				form.addEventListener("scroll", this.scrollVerticallyEnter);
			}
		}
		componentWillUnmount() {
			var form = this.form;
			if (form) {
				form.removeEventListener("scroll", this.scrollVerticallyEnter);
			}
		}
		scrollVerticallyEnter = (form)=>{
			// container form is target
			if (form.target) {
				// so we can pass to this the form or the parent object of the form
				form = form.target;
			}

			// to show tip only when at the top... 
			// adjust (form.offsetHeight + N) as the tolerance - don't show custom scrollbar and arrow when almost at the end of form - session can figure that out themselves
			if ((form.scrollTop + form.offsetHeight + 30) > form.scrollHeight) {
				form.classList.remove('scrollDown');
			} else {
				form.classList.add('scrollDown');
			}
		}

		componentDidUpdate() {
			var form = this.form;
			var { stateScope } = this.props;
			stateScope.state.luxulFormInvalid.forEach(function(inputName, index, all){
				if (!form[inputName]) {
					stateScope.state.luxulFormInvalid.splice(index,1);
				}
			});
		}

		render() {
			var { stateScope, onChange, onSubmit, children, className, ...input } = this.props;

			var onFormChange = function(event){
				if (onChange) {
					onChange(event);
				}
			};

			var onFormSubmit = function(event){
				event.preventDefault();
				if (!stateScope.state.luxulFormSubmitAttempted) {
					stateScope.setState({luxulFormSubmitAttempted:true,luxulFormSuccess:false,luxulFormError:false});
				};

				/*
					focus inputs here - if fail, do not continue
				*/
				var promise = stateScope.luxulFormValidate();

				/*
					pass through event - return sucess or failed
				*/
				onSubmit(promise);
			};

			// console.info("LuxulForm render()", JSON.stringify(stateScope.state,null," "));
			return (
				<div className="LuxulForm_customScrollbars">
					<form 
					ref={form => this.form = form}
					{...input} 
					className={ className+ " LuxulForm" +( stateScope.state.luxulFormConnectionFailed?" connectionFailed":"" ) +( stateScope.state.luxulFormLoading?" loading":"" ) +( stateScope.state.luxulFormSubmitAttempted?" submitAttempted":"" ) +( stateScope.state.luxulFormInvalid.length?" invalid":"" ) +( stateScope.state.luxulFormSubmitting?" submitting":"" ) +( stateScope.state.luxulFormTouched?" touched":"" ) +( stateScope.state.luxulFormChanged?" changed":"" ) }
					autoComplete="off" 
					onSubmit={onFormSubmit} onChange={onFormChange} onBlur={onFormChange}
					style={{overflowY:"scroll"}}
					>
						<svg className="loading_spinner" width="65px" height="65px" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
							<circle className="loading_spinner_path" fill="none" strokeWidth="6" strokeLinecap="round" cx="33" cy="33" r="30"></circle>
						</svg>
						<div>

							{children}

							{stateScope.state.luxulFormButtons &&
							<div>
								<div className="formSubmitSpacer">
									{/* this is to add space "behind" the floating buttons */}
								</div>
								<div className="formSubmit">
									{/* success */}
									{(stateScope.state.luxulFormButtons.success!==false 

										&& stateScope.state.luxulFormSuccess && stateScope.state.luxulFormSubmitAttempted) &&
										<button className={"clear ifTouched isMessage success"} type="button"><b>{stateScope.state.luxulFormButtons.success===undefined || stateScope.state.luxulFormButtons.success===true ? "Success!" : stateScope.state.luxulFormButtons.success}</b></button>
									}
									{/* error */}
									{(stateScope.state.luxulFormButtons.error!==false && 

										stateScope.state.luxulFormError && stateScope.state.luxulFormSubmitAttempted) &&
										<button className={"clear ifTouched isMessage error"} type="button"><b>{stateScope.state.luxulFormButtons.error===undefined || stateScope.state.luxulFormButtons.error===true ? "Error!" : stateScope.state.luxulFormButtons.error}</b></button>
									}
									{/* submitting */}
									{(stateScope.state.luxulFormButtons.submitting!==false && 

										stateScope.state.luxulFormSubmitting && stateScope.state.luxulFormSubmitAttempted)  &&
										<button className={"clear ifTouched isMessage success"} type="button"><b>{stateScope.state.luxulFormButtons.submitting===undefined || stateScope.state.luxulFormButtons.submitting===true ? "Submitting..." : stateScope.state.luxulFormButtons.submitting}</b></button>
									}
									{/* not valid */}
									{(stateScope.state.luxulFormButtons.invalid!==false && 

										stateScope.state.luxulFormSubmitAttempted && stateScope.state.luxulFormChanged && stateScope.state.luxulFormInvalid.length>0) &&
										<button className={"clear ifTouched isMessage invalid"} type="button"><b>{stateScope.state.luxulFormButtons.invalid===undefined || stateScope.state.luxulFormButtons.invalid===true ? <span>Form not valid. <br />Please check fields highlighted red.</span> : stateScope.state.luxulFormButtons.invalid}</b></button>
									}
									{/* loading */}
									{(stateScope.state.luxulFormButtons.loading!==false && 

										stateScope.state.luxulFormLoading) &&
										<button className={"clear isMessage invalid"} type="button"><b>Loading...</b></button>
									}
									{/* connectionFailed */}
									{(stateScope.state.luxulFormButtons.connectionFailed!==false && 

										stateScope.state.luxulFormConnectionFailed) &&
										<button className={"clear isMessage invalid"} type="button"><b>Could not connect to device. Please make sure your device is connected and powered on. Then refresh this page.</b></button>
									}
									{/* invalid */}
									{(stateScope.state.luxulFormConnectionFailed!==false && 

										stateScope.state.luxulFormSubmitAttempted && stateScope.state.luxulFormChanged && stateScope.state.luxulFormInvalid.length>0) &&
										<button className={"clear ifTouched isMessage invalid"} type="button"><b>{stateScope.state.luxulFormButtons.invalid===undefined || stateScope.state.luxulFormButtons.invalid===true ? <span>Form not valid. <br />Please check fields highlighted red.</span> : stateScope.state.luxulFormButtons.invalid}</b></button>
									}
									{/* not changed */}
									{(stateScope.state.luxulFormButtons.na!==false && 

										!stateScope.state.luxulFormChanged) &&
										<button className={"clear ifTouched isMessage"} type="button"><b>{stateScope.state.luxulFormButtons.na===undefined || stateScope.state.luxulFormButtons.na===true ? "Nothing Changed" : stateScope.state.luxulFormButtons.na}</b></button>
									}
									{/* submit ... */}
									{(stateScope.state.luxulFormButtons.submit!==false && 

										!stateScope.state.luxulFormSubmitting && !stateScope.state.luxulFormConnectionFailed && stateScope.state.luxulFormChanged && !stateScope.state.luxulFormSubmitAttempted) &&
										<button className={"" +((stateScope.state.luxulFormInvalid===undefined || !stateScope.state.luxulFormInvalid.length) ? " primary" : "")} type="submit"><b>{stateScope.state.luxulFormButtons.submit===undefined || stateScope.state.luxulFormButtons.submit===true ? "Save Changes" : stateScope.state.luxulFormButtons.submit}</b></button>
									}
									{/* reset ... */}
									{(stateScope.state.luxulFormButtons.reset!==false && 

										stateScope.state.luxulFormChanged && (JSON.stringify(stateScope.state.luxulFormValues)!==JSON.stringify(stateScope.state.luxulFormValuesOriginal))) &&
										<button className={""} type="button" onClick={stateScope.luxulFormReset}><b>{stateScope.state.luxulFormButtons.reset===undefined || stateScope.state.luxulFormButtons.reset===true ? "Reset" : stateScope.state.luxulFormButtons.reset}</b></button>
									}
								</div>
							</div>
							}

							<button className="clear scrollCue" type="button" ref={(e)=>{ stateScope.e = e; }} onClick={()=>{ $(stateScope.e.form).animate({ scrollTop: stateScope.e.form.scrollHeight+"px"}, 1000); }}>
								<b><span className="fontIcon icon-carat_down" /></b>
							</button>

						</div>
					</form>


					<div className={"LuxulHintBox "+((stateScope.state.Hints)?" opened":"")+(window.pageYOffset>10?" scrolled":"")}>
						{stateScope.state.Hints}
					</div>
				</div>
			)
		}

	},


	/*
		input
	*/
	LuxulInput: class LuxulInput extends React.Component {

		componentWillReceiveProps(){
			var { stateScope, validations, name } = this.props;

			// add this to parent form
			if (validations) {
				stateScope.luxulFormElementsToValidate = Object.assign({
					...stateScope.luxulFormElementsToValidate,
					[name]: this
				});
			}
		}

		constructor(props) {
			super(props);
			var { stateScope, validations, onChange } = this.props;

			// this
			this.state = {
				invalid: false,
				touched: false,
				changed: false
			};

			/*
				initially, validate - but update form validity only, dont mess with this input until touched by session
			*/
			this.onLoad = (event, value)=>{
				// checkbox uses checked instead of value, so it must be passed in and caught here
				// with this if statement, we can pass a pre-filtered value from any type of field as well
				if (value===undefined || typeof value === "object") {
					value = event.target.value;
				}

				// initial validate
				if (validations) {
					var luxulFormInvalid = stateScope.state.luxulFormInvalid || [];
					var invalid = this.validate(value, validations);
					var inIndex = luxulFormInvalid.indexOf(event.target.name);
					// update local
					if (invalid || invalid==="") {
						this.setState({invalid:true});
						// if name is not already in array (was valid), add
						if (inIndex===-1) {
							luxulFormInvalid.push(event.target.name);
						}
					} else {
						this.setState({invalid:false});
						// if name is already in array (was invalid), remove
						if (inIndex!==-1) {
							luxulFormInvalid.splice(inIndex,1);
						}
					}
					// update form
					stateScope.setState({
						luxulFormInvalid: luxulFormInvalid,
						luxulFormTouched: true
					});
				}
			}

			/*
				actions
			*/
			this.handleChange = (event, value)=>{

				// select and toggle get special treatment
				// they're not ok with just reading value from event.target.value
				// this if is for regular inputs, select and toggle get value passed as second parameter
				if (value===undefined || typeof value === "object") {
					value = event.target.value;
				}
				// console.log('handleChange', JSON.stringify(stateScope.state.luxulFormValuesOriginal,""," "), JSON.stringify(stateScope.state.luxulFormValues,""," "));

				// update form
				_set_property(stateScope.state.luxulFormValues, event.target.name, value);
				if (_get_property(stateScope.state.luxulFormValuesOriginal, event.target.name)===undefined) {
					_set_property(stateScope.state.luxulFormValuesOriginal, event.target.name, "");
				}
				stateScope.setState({
					luxulFormTouched: true,
					luxulFormChanged: isEqualShallow(stateScope.state.luxulFormValues, stateScope.state.luxulFormValuesOriginal)
				});

				// update input
				this.setState({
					touched: true,
					changed: (_get_property(stateScope.state.luxulFormValuesOriginal,event.target.name)!==value)
				});

				// pass through actions
				if (onChange) {
					onChange(event);
				}

				// toggle and select get special treatment
				if (event.target.type==="checkbox" || event.target.type.indexOf("select")!==-1) {
					event.target.blur();
				}
			};

			/*
				validations
			*/
			this.handleValidate = (params)=>{

				// how was this called?
				if (params.target) {
					// if this is an actual DOM event handler, then params are the event, as in an onClick
					var event = params;
				} else if (params.event) {
					// if called manually for validation, the event is passed in as a prop
					var { value, clearValidations, reject } = params;
					event = params.event; // can't re-declare - because there already is an inherent event variable floating around in this scope??? idk
				}


				// select and toggle get special treatment
				// they're not ok with just reading value from event.target.value
				// this if is for regular inputs, select and toggle get value passed as second parameter
				if (value===undefined || typeof value === "object") {
					value = event.target.value;
				}

				// clear 'submitAttempted' flag
				if (stateScope.state.luxulFormSubmitAttempted) {
					stateScope.setState({luxulFormSubmitAttempted:false});
				}

				// validate
				if (validations) {
					if (!stateScope.state.luxulFormInvalid) {
						stateScope.state.luxulFormInvalid = [];
					}
					var invalid = clearValidations ? false : this.validate(value, validations);
					var inIndex = stateScope.state.luxulFormInvalid.indexOf(event.target.name);
					// update local
					if (invalid || invalid==="") {
						this.setState({invalid:invalid});
						// if name is not already in array (was valid), add
						if (inIndex===-1) {
							stateScope.state.luxulFormInvalid.push(event.target.name);
						}
						// focus on this field
						if (window.innerWidth<=750) {
							// mobile - scroll body
							$("body").animate({
								scrollTop: $(event.target).offset().top-($(event.target.form).height()/2)
							}, 1000);
						} else {
							// desktop - scroll box
							$(event.target.form).animate({
								scrollTop: $(event.target).offset().top-$(event.target.form).offset().top-($(event.target.form).height()/2)
							}, 1000);
						}
						if (reject) {
							reject("Form is not valid.");
						}
					} else {
						this.setState({invalid:false});
						// if name is already in array (was invalid), remove
						if (inIndex!==-1) {
							stateScope.state.luxulFormInvalid.splice(inIndex,1);
						}
					}

					// update form
					stateScope.setState({
						luxulFormInvalid: stateScope.state.luxulFormInvalid,
						luxulFormTouched: true
					});
					// force update
					// when form becomes valid or invalid
					// if (!stateScope.state.luxulFormInvalid.length !== !luxulFormInvalidBefore.length) {
						// yes, force updating it every time is not an efficient or best way to do it... 
						// but, it is very simple to understand, maintain, and our app can handle it because it is so light anyway
						// still, we should be thinking of a way to do this without updating the entire form component every time
						stateScope.forceUpdate();
					// }
				}

				// toggle and select get special treatment
				if (event.target.type==="checkbox" || event.target.type.indexOf("select")!==-1) {
					this.setState({"invalid":invalid});
				}

				// return invalid
				// empty string in case validation function wants to reject a value with empty invalid message
				if (invalid || invalid==="") {
					return invalid || true;
				} else {
					return false;
				}
			}
			this.validate = function(value, validations) {

				// allow parameter to be string instead single value array
				if (typeof validations === "string") {
					validations = [validations];
				}

				// run each validation
				if (validations.length) {
					var errorMessage = false;
					validations.forEach(function(validationFunction, index){
						if (!errorMessage) {
							errorMessage = validationFunction(value);
							// console.warn(errorMessage);
							if (errorMessage) {
								// tell the form
								return;
							}
						}
					});
					// return first fail, or false if ok
					return errorMessage || false;
					// this.setState({invalid: errorMessage || false });
				}
			};
		}

		render() {
			var { stateScope, onChange, validations, value, options, ...input } = this.props;

			// ignore this >>>
			// need to declare above: onChange, validations, value, options 
			// so they will not sometimes be part of the desctructured variable: ...input, also above  
			if (stateScope || onChange || validations || value || options) { /* can't silence Webpack React no-unused-vars warning, but these values must always be declared */ }
			// <<< ignore this

			const Messages = (this.state.invalid || this.state.invalid==="") ? <div className="control_message invalid">{this.state.invalid}</div> : "";
			
			const formValue = _get_property(stateScope.state.luxulFormValues, input.name);
			
			return (
				<div className={ "control LuxulInput" +( this.state.invalid?" invalid":"" ) +( this.state.touched?" touched":"" ) +( this.state.changed?" changed":"" ) }>
					<input {...input} value={formValue} onChange={this.handleChange} onBlur={this.handleValidate} />
					{Messages}
				</div>
			)
		}

	},

	/*
		hint tip
	*/
	LuxulHint: class LuxulHint extends React.Component {
		constructor(props){
			super(props);
			this.handleHint = handleHint.bind(this.props.stateScope);
		};
		render() {
			const Span = styled.span``;
			// 	&.red_border {
			// 		color: ${window.};
			// 		font-size: 1em;
			// 		margin: 1em;
			// 		padding: 0.25em 1em;
			// 		border: 2px solid palevioletred;
			// 		border-radius: 3px;
			// 	}
			// 	.LuxulHint {
			// 		background:blue;
			// 		color:white;
			// 	}
			// `;
			var { title, description, label } = this.props;
			return (
				<Span className="red_border" onClick={()=>{ this.handleHint(title, description); }} style={{cursor:"pointer"}}>
					<sup className="LuxulHint fontIcon icon-pk_question" /> 
					{label && 
						<sup> {label}</sup>
					}
				</Span>
			)
		}

	}

}

moduleExports.LuxulSelect = class LuxulSelect extends moduleExports.LuxulInput {
		render() {
			var { stateScope, onChange, validations, value, options, children,  ...input } = this.props;

			// ignore this >>>
			// need to declare above: onChange, validations, value, options 
			// so they will not sometimes be part of the desctructured variable: ...input, also above
			if (onChange || validations || value || options) { /* can't silence Webpack React no-unused-vars warning, but these values must always be declared */}
			// <<< ignore this

			const formValue = _get_property(stateScope.state.luxulFormValues, input.name);

			return (
				<div className={ "control LuxulInput LuxulSelect" +( this.state.invalid?" invalid":"" ) +( this.state.touched?" touched":"" ) +( this.state.changed?" changed":"" ) }>
					<select {...input} value={formValue} onChange={this.handleChange} onBlur={this.handleValidate}>
						{children}
					</select>
					{(this.state.invalid || this.state.invalid==="") && <div className="control_message invalid">{this.state.invalid}</div>}
				</div>
			)
		}
}

moduleExports.LuxulToggle = class LuxulToggle extends moduleExports.LuxulInput {

		render() {
			var { stateScope, onChange, onBlur, validations, value, options, checked, toggled, labelOn, labelOff, ...input } = this.props;

			// ignore this >>>
			// need to declare above: onChange, validations, value, options, checked, toggled
			// so they will not sometimes be part of the desctructured variable: ...input, also above
			if (onChange || onBlur || validations || value || options || checked || toggled) {} /* can't silence Webpack React no-unused-vars warning, but these values must always be declared */
			// <<< ignore this
		
			var handleChange = (event)=>{
				var value = event.target.checked;
				this.handleChange(event, value);
				if (onChange) {
					// onChange(event, value);
				}
			}
			var handleBlur = (event)=>{
				var value = event.target.checked;
				this.handleValidate({event, value});
				if (onBlur) {
					// onChange(event, value);
				}
			}

			const formValue = _get_property(stateScope.state.luxulFormValues, input.name);

			return (
				<div className={ "control LuxulInput LuxulToggle" +( this.state.invalid?" invalid":"" ) +( this.state.touched?" touched":"" ) +( this.state.changed?" changed":"" ) }>
					<label className="toggle">

						{labelOff && 
							<span className="labelOff">{labelOff}</span>
						}
						<input type="checkbox" {...input} checked={formValue || false} value={true} onChange={handleChange} onBlur={handleBlur} />
						<span className="lever"></span> 
						{labelOn && 
							<span className="labelOn">{labelOn}</span>
						}

					</label>
					{(this.state.invalid || this.state.invalid==="") && <div className="control_message invalid">{this.state.invalid}</div>}
				</div>
			)
		}
}


module.exports = moduleExports;


