// Temporary, to bypass connecting to device and login screen...
// !!!
// Also commented out Auth in Layout.js
// !!!
import * as deviceInfo from '../public/devices/XWC-1000v1.json';

	// parse data
	// make available globally in "window.device" variable, to be used later
	// or pass it to each <Route/> and their children as Context, though using a window variable is much simpler and does the job just fine
	window.device = deviceInfo.default;

	// build routes
	const Routes = [];

			// login page
			window.device.login = {
				url:"/login",
				component:"session/PageLogin",
				title:"Login",
				icon:"icon-account",
			};

			// page not found
			window.device.error404 = {
				url:"*",
				component:"000/404",
				title:"Page Not Found",
				icon:"icon-account"
			};

			// default route
			Routes.push(<Redirect key="routeRedirect" from="/" to={window.device.routes[0].url} />);
			Routes.push(<Route key="route-login" ubus={ubus} path="/login" component={function(){ return require('./pages/'+window.device.login.component).default; }} page={window.device.routes.login} />);
			Routes.push(<Route key="route-error404" ubus={ubus} path="/error404" component={function(){ return require('./pages/'+window.device.error404.component).default; }} page={window.device.routes.error404} />);


	// loop array of data (lowercase): window.device.routes
	// add to array of components (capitalized): Routes
	for (let i = 0; i < window.device.routes.length; i++) {
		// 1st level nav/url
		// /example: quicksetup
		if (window.device.routes[i].component) {
			Routes.push(<Route key={"route"+i} ubus={ubus} path={window.device.routes[i].url} component={function(i){ return require('./pages/'+window.device.routes[i].component).default; }(i)} page={window.device.routes[i]} />);
		}
		if (window.device.routes[i].children) {
			for (let ii = 0; ii < window.device.routes[i].children.length; ii++) {
				// 2nd level nav/url
				// example: /wifi/connect
				if (window.device.routes[i].children[ii].component) {
					Routes.push(<Route key={"route"+i+"."+ii} ubus={ubus} path={window.device.routes[i].children[ii].url} component={function(i, ii){ return require('./pages/'+window.device.routes[i].children[ii].component).default; }(i, ii)} page={window.device.routes[i].children[ii]} />);
				}
			}
		}
	}

	// re-render page, now that we know what modules to use
	ReactDOM.unmountComponentAtNode(document.getElementById('app'));
	ReactDOM.render(
		<Router history={browserHistory}>
			{Routes}
		</Router>,
		document.getElementById('app')
	);







