// no flow here - incompatible - use flow in every other file though

/*
	1) define default properties
	* optionally define sub-properties, so that we can look back here at any time and see what our global state should contain
	* optionally, add some comments to document each property. Again, this is only to be able to view quickly what each should be. Properties and subproperties are actually validated in step 2
*/
window.state = {
	message: {
		title: "", // string
		type: "", // string (optional)
		opened: false, // boolean, to show or hide (optional)
		supportDetails: [], // array of strings, to be displayed in <ul><li> to support the message title (optional)
	},
	popup: {
		Box: null, // React Component or null
	},
	side: {
		Box: null, // React Component or null
	},
	// form: {
	// 	loading: false, // boolean. If true then disable user interactions (optional)
	// 	submitting: false, // boolean. If true then disable user interactions (optional)
	// },
};



/*
	2) optional
	* do type checking and filtering on those properties before the value is changed
	* Proxy takes 2 arguments. First, the state object. Then, the functions (handlers) to run on it: get, set, has, construct, etc
	* https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Proxy
*/
window.state = new Proxy(
	window.state, 
	{
		set(state, name, value) {
			var oldValue = state[name];
			switch(name) {

				case "message":
					// empty by default, instead of error!
					if (typeof value !== "object") {
						value = {};
					}
					// opened by default
					if (value.title && value.opened===undefined) {
						value.opened = true;
					}
					// if no type specified, use old - this is so if you set to an empty object or false, it will close, but while closing remain the same color as before
					if (!value.type && oldValue.type) {
						value.type = oldValue.type;
					}
					break;

				case "popup":
				case "side":
					// empty by default, instead of error!
					if (typeof value !== "object") {
						value = {};
					}
					break;

				default:
					break;

			}

			/*
				debugging
			*/
			if (process && process.env.NODE_ENV && process.env.NODE_ENV === 'development') {
				window.log('${'+name+'} set',["before",oldValue],["after",value]);
			}

			/*
				done checking, set state value
			*/
			state[name] = value;
			return value;
		}
	}
);
// To make window.state as "const" instead of "var" so it cannot be deleted, create the Proxy using this code. Keeping this out for now so our code is easier to understand
// Object.defineProperty(window, 'state', {
// enumerable: false,
// configurable: false,
// writable: false,
// value: new Proxy(



/*
	3) change each variable in the global store as if it was a normal global variable

	window.state.message = {title:"Error or something!"}
*/



/*
	4) read each variable using normal Javascript

	<div>window.state.message.title</div>
*/



/*	
	5) 

	// 5a.
	// to make the variable in your React component dynamically update when its value changes, just add this bit of code...

	componentWillMount(){
		this._isMounted = true;

		// sync GLOBAL with LOCAL state, selectively:
		window.state.watch("message",(name,oldValue,value)=>{
			if (this._isMounted) {
				this.setState({[name]:value});
			}
			return value;
		});
	}

	// 5b.
	// instead of reading from window like this <div>{window.state.message.title}</div>
	// read from the local reference, which is dynamic <div>{this.state.message.title}</div>

	// 5c.
	// don't forget to unwatch whatever you watch... this._isMounted is useful for other things and is nice to have regardless

	componentWillUnmount(){
		this._isMounted = false;

		// clean up watchers
		window.state.unwatch("message");
	}
	
*/






export default {};


