/* core */
import React from 'react';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import { LuxulForm, LuxulInput, validations } from '@src/components/form/LuxulForm';


/*
	form
*/
class PageComponent extends React.Component {

	constructor() {
		super();
		this.state = {
			luxulFormValues: {
				password1: "",
				password2: ""
			}
		};
	}

	render() {

		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>

					<LuxulForm
					stateScope={this}
					>

						<div className="formSection">
							<fieldset className="info">
								<label className="">Password:</label>
								<span><LuxulInput stateScope={this} name="password1" type="password" validations={[validations.required]} /></span>
							</fieldset>
							<fieldset className="info">
								<label className="">Confirm Password:</label>
								<span><LuxulInput stateScope={this} name="password1" type="password" validations={[validations.required]} /></span>
							</fieldset>
						</div>

						<p>&nbsp;</p>

					</LuxulForm>
										
				</Box>

			</Layout>
		)
	}

};


export default PageComponent;


