/* core */
import React from 'react';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import { LuxulSelect } from '@src/components/form/LuxulForm';


/*
	form
*/
class PageComponent extends React.Component {

	constructor() {
		super();
		this.state = {
			luxulFormValues: {
				ledStatus: "1"
			}
		};
	}

	render() {

		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>

					<div className="LuxulForm">

						<div className="formSection">
							<fieldset className="info">
								<label className="">Status:</label> <span>Not Running</span>
							</fieldset>
							<fieldset className="info">
								<label className="">Run for:</label>
								<span><LuxulSelect stateScope={this} name="ledStatus"><option value="1">1 hour</option><option value="2">2 hours</option><option value="3">3 hours</option></LuxulSelect></span>
							</fieldset>
							<fieldset className="info">
								<label className=""></label>
								<span><button className="primary"><b>Start Iperf Server</b></button></span>
							</fieldset>
						</div>
							
						<p>&nbsp;</p>

					</div>
										
				</Box>

			</Layout>
		)
	}

};


export default PageComponent;


